<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/user" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">用户管理</span>
        </el-menu-item>
        <el-menu-item index="/notice" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">通知公告</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 160px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>

    <div style="padding: 20px 0 0 20px;width: 100%">
      <div>
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </div>
      <div style="margin: 10px 0">
        <el-button type="primary" plain @click="handleAdd" >新增</el-button>
        <el-button type="danger" plain @click="delBatch" v-if="user.authority==='超级管理员'">批量删除</el-button>
        <el-button type="info" plain @click="exportData">批量导出</el-button>
        <el-upload :action="$baseUrl + '/user/import'" :headers="{token: user.token}" :on-success="handleImport" style="display: inline-block;margin-left: 10px" :show-file-list="false">
          <el-button type="primary" plain >批量导入</el-button>
        </el-upload>
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="用户ID" prop="id" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="用户账号" prop="account" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="用户姓名" prop="name"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="所属部门" prop="department"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="工段" prop="workSection" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="电话" prop="phone" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="地址" prop="address" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="用户权限" prop="authority" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="操作" align="center" width="180" fixed="right">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="handleEdit(scope.row)" v-if="user.authority==='管理员' || user.authority==='超级管理员'">编辑</el-button>
            <el-button size="mini" type="danger" plain  @click="del(scope.row.id)" v-if="user.authority==='超级管理员'">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="新增用户信息" :visible.sync="fromVisible" width="40%">
        <el-form :model="form" label-width="100px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="用户账号" prop="account"><el-input v-model="form.account" placeholder="用户账号"></el-input></el-form-item>
          <el-form-item label="用户姓名" prop="name"><el-input v-model="form.name" placeholder="用户昵称"></el-input></el-form-item>
          <el-form-item label="所属部门" prop="department"><el-input v-model="form.department" placement="所属部门"></el-input></el-form-item>
          <div style="display: flex">
            <el-form-item label="电话" prop="phone"><el-input v-model="form.phone" placeholder="电话"></el-input></el-form-item>
            <el-form-item label="工段" prop="workSection"><el-input v-model="form.workSection" placeholder="工段"></el-input></el-form-item>
          </div>
          <el-form-item label="地址" prop="address"><el-input type="textarea" v-model="form.address" placeholder="地址"></el-input></el-form-item>
          <el-form-item label="用户权限" prop="authority">
            <el-select v-model="form.authority" >
              <el-option
                  v-for="item in authorityList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="头像">
            <el-upload
                class="avatar-uploader"
                :action="$baseUrl + '/file/upload'"
                :headers="{ token: user.token }"
                :file-list="form.avatar? [form.avatar] : []"
                list-type="picture"
                :on-success="handleAvatarSuccess"
            >
              <el-button type="primary">上传头像</el-button>
            </el-upload>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
      <el-dialog title="修改用户信息" :visible.sync="fromVisible1" width="40%">
        <el-form :model="form" label-width="100px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="用户账号" prop="account"><el-input v-model="form.account" placeholder="用户账号"></el-input></el-form-item>
          <el-form-item label="用户姓名" prop="name"><el-input v-model="form.name" placeholder="用户昵称"></el-input></el-form-item>
          <el-form-item label="所属部门" prop="department"><el-input v-model="form.department" placement="所属部门"></el-input></el-form-item>
          <div style="display: flex">
            <el-form-item label="电话" prop="phone"><el-input v-model="form.phone" placeholder="电话"></el-input></el-form-item>
            <el-form-item label="工段" prop="workSection"><el-input v-model="form.workSection" placeholder="工段"></el-input></el-form-item>
          </div>
          <el-form-item label="地址" prop="address"><el-input type="textarea" v-model="form.address" placeholder="地址"></el-input></el-form-item>
          <el-form-item label="用户权限" prop="authority" v-if="user.authority==='超级管理员'">
            <el-select v-model="form.authority" >
              <el-option
                  v-for="item in authorityList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="头像">
            <el-upload
                class="avatar-uploader"
                :action="$baseUrl + '/file/upload'"
                :headers="{ token: user.token }"
                :file-list="form.avatar? [form.avatar] : []"
                list-type="picture"
                :on-success="handleAvatarSuccess"
            >
              <el-button type="primary">上传头像</el-button>
            </el-upload>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>

  </div>
</template>

<script>
export default {
  name: "User",
  data() {
    return {
      searchText: '',
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      account: '',
      total: 0,
      fromVisible: false,
      fromVisible1: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      // departmentList: [
      //   {value: '设备部', label: '设备部'},
      //   {value: '动力车间', label: '动力车间'},
      //   {value: '技术部', label: '技术部'}
      // ],
      authorityList: [
        {value: '超级管理员', label: '超级管理员'},
        {value: '管理员', label: '管理员'},
        {value: '点检员', label: '点检员'},
        {value: '维修工', label: '维修工'},
        {value: '安全员', label: '安全员'}
      ],
      ids: []
    }
  },
  created() {
    this.load()
  },
  methods: {
    handleImport(res,file,fileList){
      if (res.code ==='200'){
        this.$message.success("操作成功")
        this.load(1)
      }else {
        this.$message.error(res.msg)
      }
    },
    exportData(){   //批量导出数据
      if (!this.ids.length){  //没有选择行的时候，全部导出，或者根据我的搜索条件导出
        window.open(this.$baseUrl+'/user/export?token=' + this.user.token + "&account=" + this.account)
      } else {   //[1,2,3] => '1,2,3'
        let idStr = this.ids.join(',')
        window.open(this.$baseUrl+'/user/export?token=' + this.user.token + '&ids=' + idStr)

      }

},
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/user/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/user/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible1 = true   // 打开弹窗
    },
    handleAdd() {   // 新增数据
      this.form = { role: '一般员工' }  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/user/update': '/user/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 表示成功保存
              this.$message.success('保存成功')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 弹出错误的信息
            }
          })
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 分页查询

      this.$request.get('/user/getDepartmentList').then(res=>{console.log(res.data)})

      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/user/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          account: this.account,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
    handleAvatarSuccess(response, file, fileList) {
      // 把user的头像属性换成上传的图片的链接
      this.form.avatar = response.data
    },
  }
}
</script>

<style scoped>

</style>